const exampleData = {
    _id: 'klsdfmglksdfgmlfdg',
    path: 'nodejs-mongoose-truck',
    cover: "https://miro.medium.com/max/700/1*qQwwv7aYYcjY1q42pOzPFw.jpeg",
    youtubeEmbedUrl: "https://www.youtube.com/embed/vPL46ZdAZrA",
    categories: [{
        path: '/posts',
        title: `Yazılar`
    }, {
        path: '/posts/yazilim',
        title: `Yazılım`
    }, {
        path: '/posts/yazilim/nodejs',
        title: "Node.js",
    }],
    title: `Node.js — MongoDb Veri Taşıma İşlemleri (mongoose & mongoose-truck)`,
    subtitle: `Node.js ve MongoDb kullanarak MongoDb veritabanındaki bir collection ‘dan diğer MongoDb veritabanlarına veri taşımak için mongoose-truck kullanılır.`,
    htmlContent: `\n<h3>Node.js — <strong>MongoDb</strong> veritabanındaki bir collection ‘dan diğer MongoDb veritabanlarına veri taşımak (mongoose &amp; mongoose-truck)</h3>\n<figure><img alt=\"\" src=\"https://cdn-images-1.medium.com/max/1024/1*qQwwv7aYYcjY1q42pOzPFw.jpeg\"></figure><p>Merhabalar sayın okucular, bugün sizlere yeni açık kaynak kodlu projemi anlatmak istiyorum. Öncelikle bu projenin nasıl çıktığından bahsetmem gerekiyor. Proje tam olarak bir ürün MongoDb veritabanından belirli bir veri setini başka bir test, geliştirme veritabanına veri taşırken yaşadığım stresten ortaya çıktı. Elimde bir <strong>MongoDb</strong> bağlantı text’i vardı. Buna MongoSh ile bağlanıp her seferinde sayfalandırma için gerekli olan script ’leri ekrana yapıştırmam gerekiyordu. Bunun yerine bir <strong>Node.js</strong> script’i ile bunu çözebileceğimi bilirken bunu yapmak gereksiz bir şeydi. Sonra spesifik bir <strong>Schema</strong> üzerinde çalışan bir model devreye aldım fakat bunu her Schema için tekrar etmem gerekiyordu. Buna ek olarak ürün <strong>mongoose.connect</strong> kullanıyordu bu da kodu herhangi bir route’a bağladığımda veya ürünün herhangi bir yerinde çalıştırdığımda mongoose bana hata verecekti. Çünkü ayrı bir script dosyası kullanılmazsa aynı <strong>Schema</strong> üzerinden yeni bir model başlatılınca mongoose sana bir engel oluşturur.</p>\n<p>Ben de <strong>MongoDb</strong> ’lere dinamik şekilde bağlantı oluşturacak bir paket ve bağlantıyı gerçekleştirecek bir modül oluşturdum. Bu modül bağlantı string ‘ini alır ve size içerisinde dependency olarak bulunan mongoose üzerinden bir veritabanı bağlantısı döner. Bu bağlantıları kullanarak <strong>Schema</strong> ’lar üzerinden dinamik <strong>modeller</strong> oluşturulur. Veri taşıma işlemi bu <strong>dinamik modeller</strong> üzerinden gerçekleştir. Tabii ki bu yapıyı kafamda oturtmak benim için uzun bir süre (2 gün 🙃 ) aldı.</p>\n<p>Modülü 1 gün içinde yazmayı becerebildim ve kendisi şu anda hem <a href=\"https://www.github.com/cangokceaslan/mongoose-truck\"><strong>GitHub</strong></a><strong> registry</strong> hem de <a href=\"https://www.npmjs.com/package/mongoose-truck\"><strong>npm</strong> <strong>registry</strong></a> üzerinde açık kaynak kod ve paket olarak bulunmaktadır. Buna ek olarak bu modül üzerinde çalışmak istediğim ve farklı multi-threaded işlemler için geliştirmeler bazı geliştirmeler var. Bunları da <a href=\"https://github.com/cangokceaslan/mongoose-truck/projects/1\"><strong>GitHub projeler</strong></a> özelliğini kullanarak herkesin görmesini sağlamak istiyorum.</p>\n<figure><img alt=\"\" src=\"https://cdn-images-1.medium.com/max/1000/1*isAY-FwBJqZpTnDzQv1mBg.jpeg\"></figure><p>Şimdi birazcık paketin nasıl çalıştığından bahsetmek istiyorum. Paket genel olarak bir yükün bir kamyonla taşınması gibi bir analoji kullanıyor. Önce <strong>kaynak</strong> ve <strong>hedef</strong> noktalarla bağlantı kuruluyor. Daha sonrasında kaynaktan yük yükleme işlemi için direktifler veriliyor. Bundan hemen sonra yükün hedef noktalara nasıl döküleceği ile ilgili direktifler veriliyor ve işlemler başlatılıyor.</p>\n<p>İlk olarak modülü npm ile yüklememiz gerekiyor</p>\n<blockquote>npm i mongoose-truck</blockquote>\n<pre>const MongooseTruck = require(\"mongoose-truck\")</pre>\n<pre>const conn1 = await MongooseTruck.connect(\"database_connection_string\"),<br>        conn2 = await MongooseTruck.connect(\"database_connection_string\"),<br>        conn3 = await MongooseTruck.connect(\"database_connection_string\");</pre>\n<p>Yukarıdaki kodda paketi çağırıyoruz. <strong>conn1</strong> bizim için kaynak bağlantısını sembolize ediyor. <strong>conn2</strong> ve <strong>conn3</strong> ise yükü boşaltacağımız noktaları temsil etmektedir</p>\n<pre>const parsedData = await MongooseTruck.create(conn1, [conn2, conn3]).loadFrom({<br>    model: 'People',<br>    schema: PeopleSchema,<br>    query: {<br>        name: \"John\"<br>    },<br>    populate: {<br>        path: 'stars'<br>    },<br>    skip: 0,<br>    sort: {<br>        _id: -1<br>    },<br>    select: \"_id name surname age\",<br>    concurrent: 20,<br>    limit: 100<br>}).emptyTo({<br>    model: 'People',<br>    schema: PeopleSchema<br>}).start({<br>    parse: true<br>})<br><br>console.log(parsedData);</pre>\n<p>Bu kısım ise en basit şekli ile conn1'den <strong>.loadFrom</strong> ile yüklemeye başla <strong>.emptyTo</strong> ile conn2 ve conn3 'teki boşaltılacak yeri belirle ve <strong>.start</strong> ile işleme başla olarak düşünülebilir. .<strong>loadFrom </strong>içerisindeki key ‘ler verilerin ne şekilde çekileceğini belirler.</p>\n<pre>model       // Modelin ismi - string</pre>\n<pre>schema      // Collection 'un Schema 'sı - mongoose.Schema</pre>\n<pre>query:      // Veri çekilirken kullanılacak query - object</pre>\n<pre>populate:   // Veri çekilirken eklenecek ilişkiler oluşturulur .populate() methodu ile aynı - object</pre>\n<pre>skip:       // Veri çekilmeden N tane değer pas geçilir - .skip() methodu ile aynı - number</pre>\n<pre>sort:       // Sıralama yapar - .sort() methodu ile aynı - object</pre>\n<pre>select:     // Veri çekilirken gelecek key'ler - .select() methodu ile aynı - object / string</pre>\n<pre>concurrent: // Veriyi çekerken kullanılacak sayfalandırma sabiti - number</pre>\n<pre>limit:      // Toplam çekilecek veri miktarı - .limit() methodu ile aynı - number</pre>\n<p>Birden fazla kaynaktan veri çekilerek de bu işlemler gerçekleştirilebilir. Örneğin: Kullanıcı collection ‘larım var birden fazla ben ikisini de kullanmak istiyorum. Başka bir örnek olarak aynı collection ‘dan iki farklı query kullanarak veri çekmek istiyorum. Bu durumda ise</p>\n<pre>const parsedData = await MongooseTruck.create(conn1, [conn2, conn3]).loadFrom([{<br>        model: 'People',<br>        schema: PeopleSchema,<br>        query: {<br>            name: \"John\"<br>        },<br>        skip: 0,<br>        sort: {<br>            _id: -1<br>        },<br>        concurrent: 5,<br>        limit: 10<br>    },<br>    {<br>        model: 'People',<br>        schema: PeopleSchema,<br>        query: {<br>            name: \"Adam\"<br>        },<br>        populate: {<br>            path: 'stars'<br>        },<br>        skip: 1,<br>        sort: {<br>            _id: -1<br>        },<br>        select: \"_id name surname age\",<br>        concurrent: 3,<br>        limit: 20<br>    }<br>]).emptyTo({<br>    model: 'PersonsTests',<br>    schema: Person<br>}).start({<br>    parse: true<br>})<br>console.log(parsedData);</pre>\n<p>Görüldüğü gibi .<strong>loadFrom </strong>içerisine bir array alabilir. Farklı collection ‘lardaki verileri tek bir yere toplarken veya tek bir collection ‘dan farklı query ’lerle veri çekilmek istenildiğinde bu şekilde array haline getirilebilir.</p>\n<p><strong>.start</strong> işlemi içerisine <strong>parse</strong> isimli bir key alır. Bu key ise veri geçişleri tamamlandığında verinin koda getirilip getirilmeyeceğini belirler. <strong>parse:false </strong>olarak kullanılırsa boş array dönülür.</p>\n<p>Bu bölümde de author’u olduğum mongoose kullanan <strong>Node.js</strong> uygulamalardaki iki veya daha çok MongoDb veritabanı arasındaki veri taşımanın kolay yolunu görmüş bulunmaktayız. Paketin GitHub sayfasına ulaşmak için</p>\n<p><a href=\"https://www.github.com/cangokceaslan/mongoose-truck\">GitHub - cangokceaslan/mongoose-truck: mongoose-truck is a Node.js package for handling with data transaction between different MongoDb clusters.</a></p>\n<p>Npm sayfası:</p>\n<p><a href=\"https://www.npmjs.com/package/mongoose-truck\">mongoose-truck</a></p>\n`,
    tags: ['node.js', 'mongoose', 'database', 'nodejs', 'collection', 'mongoose-truck'],
    createdAt: "2020-05-20T12:00:00.000",
    updatedAt: "2020-05-20T12:00:00.000",
    loaded: false
}



const NewCourseData: CourseType = CoursesData[0];

function EmbedYoutube(props: { url: string | undefined }) {
    return (<iframe
        src={`${props?.url}?width=1200&height=9999&muted&autoplay&origin=https://www.cangokceaslan.com`}
        title="YouTube video player"
        frameBorder="0"
        className='article-embed-youtube-video'
        allow="accelerometer;  encrypted-media; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen></iframe>)
}

function EmbedCover(props: { url: string }) {
    return (
        <img className='embed-cover-photo' src={props?.url} alt="" />
    )
}


function BlogPostNoSidebar(props: any) {
    const history = useHistory();
    const [postData, setPostData] = useState(exampleData);
    useEffect(() => {
        props.getPostFromApi({
            id: props.match.params.id
        }).then((res) => {
            if (res?.node?.post) {
                setPostData({ ...res?.node?.post, loaded: true });
            } else {
                history.push('/')
            }
        })
    }, []);
    if (!postData?.loaded) {
        return <PageLoader loaded={false} />
    }
    const data: BlogPostNoSidebarProps = {
        id: postData?._id,
        path: '/post/' + postData?.path,
        cover: postData?.cover,
        youtubeEmbedUrl: postData?.youtubeEmbedUrl,
        categories: postData?.categories,
        title: postData?.title,
        subtitle: postData?.subtitle,
        createdAt: `Paylaşılma Tarihi: ${moment(new Date(postData?.createdAt).toString()).format('LL')}`,
        updatedAt: `Güncellenme Tarihi: ${moment(new Date(postData?.updatedAt).toString()).format('LL')}`,
        htmlContent: postData?.htmlContent,
        tags: postData?.tags
    };
    const categoryBuilder = () => {
        return (data?.categories || []).map((category: {
            title: string,
            path: string
        }, index: number) => {
            if (index >= data?.categories?.length - 1) {
                return <React.Fragment>
                    <a className='category-link' href={`${category?.path}`}>{category?.title}</a>
                </React.Fragment>
            }
            return <React.Fragment>
                <a className='category-link' href={`${category?.path}`}>{category?.title}</a>
                <div className='category-link'>/</div>
            </React.Fragment>
        })
    }

    const docData: SeoSettingsType = {
        title: data?.title + ' | Can Gökçeaslan',
        description: data?.subtitle,
        image: data?.cover,
        siteName: 'https://www.cangokceaslan.com' + data?.path,
        keywords: (data?.tags || []).join(','),
        author: 'Can Gökçeaslan',
        locale: "tr"
    }
    return (
        <React.Fragment>
            <DocumentSettings docData={docData} />
            <div className="blog-content-screen-container-no-side-bar">
                <div className='blog-content-screen-container'>
                    <div className='container-fluid post-and-side-container-fluid'>
                        <div className='container post-and-side-container'>
                            <div className='row post-and-side-row' style={{
                                display: "flex",
                                justifyContent: "center"
                            }}>
                                <div className="col-lg-1 col-md-12"></div>
                                <div className='col-lg-9 col-md-12 post-container'>
                                    {data?.youtubeEmbedUrl && (<EmbedYoutube url={data?.youtubeEmbedUrl} />)}

                                    <h1 className="title-container">
                                        {data?.title}
                                    </h1>
                                    <hr />
                                    <div className="date-container">
                                        {data?.createdAt}  &nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;&nbsp;
                                        {data?.updatedAt}
                                    </div>
                                    <hr />
                                    <h2 className="subtitle-container">
                                        {data?.subtitle}
                                    </h2>
                                    {!data?.youtubeEmbedUrl && (<EmbedCover url={data?.cover} />)}

                                    {
                                        /* Math.random() >= 0.5
                                            ? (<EmbedYoutube url={data?.youtubeEmbedUrl} />)
                                            : (<EmbedCover url={data?.cover} />) */
                                    }
                                    {/* <div className='category-breadcrumb'>
                                        {categoryBuilder()}
                                    </div> */}
                                    <hr />
                                    <article dangerouslySetInnerHTML={{
                                        __html: data?.htmlContent
                                    }}>

                                    </article>
                                </div>
                                <div className="col-lg-1 col-md-12"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <NewCourse />
        </React.Fragment>
    )
}


export default connect((state: any) => ({
}), { getPostFromApi })(withRouter(BlogPostNoSidebar));